export function searchCity(cityName) {
  let result = [];
  return fetch('https://search.heweather.net/find?key=e32399712c2e4610a92fe312ff544253&group=cn&location=' + cityName).then(response => response.json()).then(res => {
    res = res.HeWeather6[0];
    if (res.status === 'ok') {
      result = res.basic.filter(item => item.parent_city.includes(cityName) && item.parent_city === item.location);
      if (!result.length) {
        result = [{location: '未搜索到结果', cid: null}];
      }
    } else {
      result = [{location: '未搜索到结果', cid: null}];
    }
    return result;
  });
}
