<template>
  <transition>
    <div class="select-city-container" v-show="showSelect">
      <div class="select-city">
        <span class="close" @click="closeSelect">×</span>
        <input type="text" class="input"
               placeholder="请输入城市名"
               v-model="searchCity"
               @focus="focus" @blur="blur">
        <div class="hot">
          <h4 class="title">热门城市</h4>
          <ul class="hot-list">
            <li v-for="item in hot" @click="changeCity(item)">{{item.location}}</li>
          </ul>
        </div>
        <ul class="result-list" v-show="inputFocus && result.length">
          <li v-for="item in result" @click="changeCity(item)">{{item.location}}</li>
        </ul>
      </div>
    </div>
  </transition>
</template>
<script>
  import { searchCity } from '../assets/js/api'
  export default {
    name: "SelectCity",
    props: {
      showSelect: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        hot: [],
        searchCity: '',
        result: [],
        inputFocus: false
      }
    },
    methods: {
      closeSelect() {
        this.$emit('close');
      },
      focus() {
        this.inputFocus = true;
      },
      blur() {
        this.timer && clearTimeout(this.timer);
        this.timer = setTimeout(() => {
          this.inputFocus = false;
        }, 200)
      },
      changeCity(city) {
        this.$emit('changeCity', city);
        this.searchCity = '';
        this.closeSelect();
      }
    },
    created() {
      fetch('https://search.heweather.net/top?group=cn&key=e32399712c2e4610a92fe312ff544253').then(response => response.json()).then(res => {
        this.hot = res.HeWeather6[0].basic;
      });
      this.timer = null;
    },
    watch: {
      searchCity(newVal) {
        if (newVal) {
          searchCity(newVal).then(res => {
            this.result = res;
          });
        } else {
          this.result = []
        }
      }
    }
  }
</script>
<style scoped lang="scss">
  .select-city-container {
    position: fixed;
    background-color: rgba(0,0,0,.5);
    left: 0;
    bottom: 0;
    right: 0;
    top: 0;
    .select-city {
      height: 400px;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      background-color: #243040;
      padding: 0 100px;
      overflow: hidden;
      .input {
        width: 500px;
        height: 50px;
        background-color: transparent;
        border: 1px solid #4a85c3;
        margin-top: 80px;
        padding: 0 16px;
        font-size: 16px;
        color: #eee;
        margin-bottom: 60px;
        &::placeholder {
          color: #aaa;
        }
      }
      .hot {
        color: #ddd;
        .title {
          border-bottom: 1px solid #4a85c3;
          padding-bottom: 10px;
        }
        .hot-list {
          display: flex;
          margin-top: 20px;
          li {
            margin-right: 16px;
            cursor: pointer;
            padding-bottom: 2px;
            font-size: 15px;
            &:hover {
              border-bottom: 1px solid #4a85c3;
              color: #4a85c3;
            }
          }
        }
      }
      .result-list {
        position: absolute;
        top: 131px;
        width: 532px;
        background: #243040;
        border: 1px solid #4a85c3;
        max-height: 200px;
        overflow: auto;
        &::-webkit-scrollbar {
          width: 6px;
        }
        &::-webkit-scrollbar-thumb {
          background-color: #4a85c3;
          border-radius: 5px;
        }
        &::-webkit-scrollbar-track {
          background-color: rgba(0,0,0,.3);
        }
        li {
          line-height: 50px;
          padding: 0 16px;
          border-bottom: 1px solid #4a85c3;
          &:last-child {
            border-bottom: none;
          }
        }
      }
      .close {
        position: absolute;
        right: 14px;
        font-size: 40px;
        cursor: pointer;
        &:hover {
          color: #4a85c3;
        }
      }
    }
  }
  .v-enter-active, .v-leave-active {
    transition: opacity .5s;
  }
  .v-enter, .v-leave-to {
    opacity: 0;
  }
</style>
