<template>
  <div id="app">
    <div id="changeCity" @click="showCty">
      <span>[切换城市]</span>
    </div>
    <div id="position">
      <span v-if="city.length"><i class="iconfont icon-pos"></i>{{city}}</span>
      <span v-else>正在定位 . . .</span>
    </div>
    <p id="title">实时天气查询</p>
    <div id="container">
      <div class="left">
        <div class="cover">
          <div class="position">
            <p class="city">{{city}}</p>
            <p>中国</p>
          </div>
          <div class="date-container">
            <p class="time">{{now}}</p>
            <p class="date">{{new Date().toDateString()}}</p>
          </div>
          <div class="temperature-container" v-if="today">
            <p class="temperature">{{today.tem}}</p>
            <p>{{today.wea}}</p>
          </div>
        </div>
      </div>
      <div class="right">
        <div class="tem-container">
          <temperature :tem="today.wea_img"></temperature>
          <div class="info" v-if="today">
            <p>空气质量：{{today.air_level}}</p>
            <p>空气指数：{{today.air}}</p>
            <p>白天温度：{{today.tem1}}</p>
            <p>夜晚温度：{{today.tem2}}</p>
            <p>湿度：{{today.humidity}}</p>
            <p>风速：{{today.win_speed}}</p>
          </div>
        </div>
        <div class="advice-container" v-if="advice.length">
          <ul>
            <li v-for="item in advice" class="advice-list">
              <p><i :class="item.className"></i> {{item.title}}：{{item.level}}</p>
              <p class="advice">小贴士：{{item.desc}}</p>
            </li>
          </ul>
        </div>
        <div class="future">
          <h4 class="future-title">未来天气</h4>
          <ul class="future-list">
            <li v-for="(item, index) in future">
              <h5 class="title">{{item.week}}</h5>
              <p class="tem">{{item.tem2}}~{{item.tem1}}</p>
              <p class="wea">{{item.wea}}</p>
              <div class="line" v-show="index < 3"></div>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <select-city :showSelect="showWindow" @close="closeWindow" @changeCity="changeCity"></select-city>
  </div>
</template>
<script>
  import SelectCity from './component/SelectCity'
  import Temperature from './component/Temperature'
  import { searchCity } from './assets/js/api'
  export default {
    name: 'app',
    data() {
      return {
        now: '00:00:00',
        today: {},
        city: '',
        advice: [],
        future: [],
        showWindow: false,
        cityId: ''
      }
    },
    methods: {
      formatAdvice(advice) {
        advice = advice.filter(item => (item.level !== null) && !item.title.includes('血糖'));
        advice.forEach(item => {
          if (item.title.includes('紫外线')) {
            item.className = 'iconfont icon-sun';
          } else if (item.title.includes('穿衣')) {
            item.className = 'iconfont icon-clothes';
          } else if (item.title.includes('洗车')) {
            item.className = 'iconfont icon-car';
          } else if (item.title.includes('空气')) {
            item.className = 'iconfont icon-air';
          }
        });
        return advice;
      },
      showCty() {
        this.showWindow = true;
      },
      closeWindow() {
        this.showWindow = false;
      },
      changeCity(city) {
        this.city = city.location + '市';
        this.cityId = city.cid.slice(2);
      }
    },
    components: {
      SelectCity,
      Temperature
    },
    created() {
      setInterval(() => {
        this.now = new Date().toTimeString().slice(0, 8);
      }, 1000);
      fetch('https://restapi.amap.com/v3/ip?key=c13174ea5cf97b75cf9487b6e53cfd0f').then(response => response.json()).then(res => {
        this.city = res.city;
        searchCity(res.city.replace('市', '')).then(result => {
          this.cityId = result[0].cid.slice(2);
        });
      });
    },
    watch: {
      cityId(newVal) {
        if (newVal) {
          fetch('https://www.tianqiapi.com/api/?version=v1&appid=44534963&appsecret=9vB33JgS&cityid=' + newVal).then(response => response.json()).then(res => {
            this.today = res.data[0];
            this.advice = this.formatAdvice(res.data[0].index);
            this.future = res.data.slice(1);
          });
        }
      }
    }
  }
</script>
